/* Section 5 - Footer Area */
.site-footer{background: #333333;padding: 5px 0;}
.site-info{background: #333333;border-top: 2px inset #999;color: #fff;padding: 5px 15px;}
.site-info a, .site-info a:visited{color: #008CBA;}
ul.footerlist li{border-right: 1px inset #777;}
ul.footerlist li:last-child{border-right:none;}
.site-footer-grid{padding: 10px 0;}
ul.footer-connect li{border-right: none;}
.footermenu .menu{list-style: none;}
.footermenu .menu > li > a{color: #fff;font-size: 1.3rem;}
.footermenu ul li{border-right: none;}
li.recent-events div.event-background{background: none;padding: 0;}
li.recent-events div.event-background h4.widget-event-title{color: #fff;}
li.recent-events div.event-background p{color: #fff;}
li.recent-events div.event-background p.event-time{margin-left: 0px;margin-top: 0px;font-size: 1.1rem;} 
li.recent-events div.event-background a h5.event-title{color:#008CBA;}
.footer-social-bar{margin-left: auto;margin-right: auto;display: block;text-align: center;}